import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';



class Post extends React.Component {  //Post element do display user's posts
    constructor(props) {
        super(props);
        this.state = {
            baseId : 'current-',
        }
    }

    handleRemoveClick() {
        var elem = document.getElementById(this.state.baseId+this.props.id);
        elem.remove();
    }

    render() {
        return(
        <div id={this.state.baseId+this.props.id}>
            <h2>{this.props.title}     <button onClick={() => this.handleRemoveClick()}>Delete Post</button></h2>
            <p>{this.props.body}</p>
        </div>
       );
    }
}

class Form extends React.Component {  //Displays Header for page, renders posts, and takes input from 'create post' form
    constructor(props) {
        super(props);
        this.state = {
            title: null,
            body: null,
            posts: [],
            counter: -1,
        }

    }

    renderPost(title, body, id) { //Renders each post element
        this.setState({
            counter: this.state.counter + 1,
        });
        return(
            <Post title={title} body={body} id={id} />
        );    
    }

    handleTitleChange(event) {  //Handles title field change
        this.setState({
            title: event.target.value,
        });
    }

    handleBodyChange(event) { //Handles body field change
        this.setState({
            body: event.target.value,
        });
    }

    handleSubmit(event) { //Handles form submit by clearing title and body fields and pushing new Post element to posts array
        event.preventDefault();
        
        this.setState({
            posts: [...this.state.posts, this.renderPost(this.state.title, this.state.body, this.state.counter)],
            title: '',
            body: '',
        });
        localStorage.setItem('posts', JSON.stringify(this.state.posts));
    }

    render() {
        return(
        <div>
            <h1 className='header'>Welcome to an Open Forum on Climate Change!</h1><br />
            <div className="posts">{this.state.posts}</div>
            <div className='form'>
                <form className='postForm' onSubmit={(event) => this.handleSubmit(event)} >
                    <h3>Post Title:</h3>
                    <input onChange={(event) => this.handleTitleChange(event)} type='text' className="title" size='50' maxLength='50' value={this.state.title} /><br />
                    <h3>Post Body:</h3>
                    <textarea onChange={(event) => this.handleBodyChange(event)} cols='70' rows='20' maxLength='1000' value={this.state.body} ></textarea><br />
                    <button type='submit'>Post</button>
                </form>
            </div>
        </div>
        );
    }
}

// =============================================
ReactDOM.render(<Form />, document.getElementById('root'));


